.genres-main {
  padding-top: 160px;
  @media (max-width: 790px) {
    padding-top: 0;
  }
}
.genres-title {
  margin-bottom: 64px;
}
.genres-image {
  height: 220px;
}
.genres-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 24px 24px 0 0;
}
.genres-content {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(290px, 1fr));
  gap: 64px 40px;
  @media (max-width: 790px) {
    gap: 15px;
  }
}
.genres-card {
  cursor: pointer;
  border-radius: 24px;
  border: 1px solid #ffffff40;
  box-shadow: 0px 0px 80px 0px #ffffff54;
  overflow: hidden;
}
.genres-name {
  font-size: 24px;
  font-weight: 700;
  color: #fff;
  padding: 22px;
  text-align: center;
}
.genres-poster {
  @media (max-width: 790px) {
    height: 502px;
  }
}
